* {
  box-sizing: border-box; }

[class*="col-"] {
  float: left; }

.row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap; }

.custom div {
  border: none !important; }

::-webkit-scrollbar {
  width: 7px;
  height: 7px; }

::-webkit-scrollbar-button {
  width: 0px;
  height: 0px; }

::-webkit-scrollbar-thumb {
  background: #b6b6b6;
  border: 0px none #ffffff;
  border-radius: 21px; }

::-webkit-scrollbar-thumb:hover {
  background: #e1e1e1; }

::-webkit-scrollbar-thumb:active {
  background: #d4d4d4; }

::-webkit-scrollbar-track {
  background: #ffffff;
  border: 0px none #ffffff;
  border-radius: 0px; }

::-webkit-scrollbar-track:hover {
  background: #ffffff; }

::-webkit-scrollbar-track:active {
  background: #ffffff; }

::-webkit-scrollbar-corner {
  background: transparent; }
